"use client";

import React from "react";
import ChannelList from "../navigation/ChannelList";
import UserList from "../navigation/UserList";

interface MainLayoutProps {
  children: React.ReactNode;
}

export default function MainLayout({ children }: MainLayoutProps) {
  return (
    <div className="flex h-screen overflow-hidden">
      {/* Sol sidebar - sunucular listesi */}
      <div className="w-[72px] h-full bg-[var(--sidebar-dark)] flex flex-col items-center py-3 gap-3">
        {/* Logo veya ana buton */}
        <div className="w-12 h-12 rounded-full bg-[var(--primary)] flex items-center justify-center text-white font-bold text-2xl">
          TS
        </div>
        
        {/* Sunucu ikonları buraya gelecek */}
        <div className="flex-1 w-full overflow-y-auto flex flex-col items-center gap-3 mt-2">
          {/* Örnek sunucular */}
          <div className="w-12 h-12 rounded-2xl bg-gray-700 flex items-center justify-center text-white hover:bg-[var(--primary)] transition-all cursor-pointer">
            G
          </div>
          <div className="w-12 h-12 rounded-2xl bg-gray-700 flex items-center justify-center text-white hover:bg-[var(--primary)] transition-all cursor-pointer">
            P
          </div>
          <div className="w-12 h-12 rounded-2xl bg-gray-700 flex items-center justify-center text-white hover:bg-[var(--primary)] transition-all cursor-pointer">
            T
          </div>
        </div>
      </div>
      
      {/* Ana içerik alanı - kanallar ve mesajlar */}
      <div className="flex flex-1">
        {/* Kanallar/DM sidebar */}
        <div className="w-60 h-full bg-[var(--sidebar)] flex flex-col">
          {/* Kanal başlığı */}
          <div className="h-12 border-b border-gray-700 flex items-center px-4">
            <h2 className="font-bold text-white">TeamSync</h2>
          </div>
          
          {/* Kanal listesi */}
          <div className="flex-1 overflow-y-auto">
            <ChannelList />
            <div className="mt-4 mx-2 border-t border-gray-700 pt-4">
              <UserList />
            </div>
          </div>
          
          {/* Kullanıcı profili */}
          <div className="h-14 bg-[var(--sidebar-dark)] p-2 flex items-center gap-2">
            <div className="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center text-white">
              U
            </div>
            <div className="flex-1">
              <p className="text-white text-sm font-medium truncate">Kullanıcı</p>
              <p className="text-gray-400 text-xs">Çevrimiçi</p>
            </div>
          </div>
        </div>
        
        {/* Mesaj alanı */}
        <div className="flex-1 flex flex-col bg-[var(--content-dark)]">
          {children}
        </div>
      </div>
    </div>
  );
} 