"use client";

import React from "react";

type Channel = {
  id: string;
  name: string;
  type: "text" | "voice";
  unreadCount?: number;
  isActive?: boolean;
};

// Örnek kanal verisi
const dummyChannels: Channel[] = [
  { id: "1", name: "genel", type: "text", isActive: true },
  { id: "2", name: "duyurular", type: "text", unreadCount: 3 },
  { id: "3", name: "proje-tartışma", type: "text" },
  { id: "4", name: "Sesli Kanal 1", type: "voice" },
];

export default function ChannelList() {
  return (
    <div className="p-2 text-gray-300">
      <div className="mb-4">
        <div className="flex items-center px-1 mb-1">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
          <h3 className="uppercase text-xs font-semibold text-gray-400 tracking-wider">Metin Kanalları</h3>
        </div>
        <ul className="space-y-[2px]">
          {dummyChannels
            .filter((channel) => channel.type === "text")
            .map((channel) => (
              <li key={channel.id}>
                <button
                  className={`w-full flex items-center rounded px-2 py-1 text-sm ${
                    channel.isActive
                      ? "bg-[#393c43] text-white"
                      : "hover:bg-[#393c43] text-gray-400 hover:text-gray-300"
                  }`}
                >
                  <span className="mr-1 text-gray-500">#</span>
                  <span className="flex-1 truncate text-left">{channel.name}</span>
                  {channel.unreadCount && (
                    <span className="bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs">
                      {channel.unreadCount}
                    </span>
                  )}
                </button>
              </li>
            ))}
        </ul>
      </div>

      <div>
        <div className="flex items-center px-1 mb-1">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
          <h3 className="uppercase text-xs font-semibold text-gray-400 tracking-wider">Sesli Kanallar</h3>
        </div>
        <ul className="space-y-[2px]">
          {dummyChannels
            .filter((channel) => channel.type === "voice")
            .map((channel) => (
              <li key={channel.id}>
                <button
                  className={`w-full flex items-center rounded px-2 py-1 text-sm ${
                    channel.isActive
                      ? "bg-[#393c43] text-white"
                      : "hover:bg-[#393c43] text-gray-400 hover:text-gray-300"
                  }`}
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                  </svg>
                  <span className="flex-1 truncate text-left">{channel.name}</span>
                </button>
              </li>
            ))}
        </ul>
      </div>
    </div>
  );
} 