"use client";

import React from "react";

type ChatHeaderProps = {
  channelName: string;
  channelDescription?: string;
  memberCount?: number;
};

export default function ChatHeader({
  channelName,
  channelDescription = "Bu kanalın bir açıklaması yok",
  memberCount = 0,
}: ChatHeaderProps) {
  return (
    <div className="h-12 px-4 border-b border-gray-700 flex items-center justify-between flex-shrink-0">
      <div className="flex items-center">
        <span className="text-lg font-semibold text-white flex items-center">
          <span className="text-gray-400 mr-1">#</span>
          {channelName}
        </span>
        {channelDescription && (
          <>
            <span className="mx-2 text-gray-600">|</span>
            <span className="text-sm text-gray-400 truncate max-w-md">
              {channelDescription}
            </span>
          </>
        )}
      </div>
      
      <div className="flex items-center gap-3">
        {/* Üye sayısı */}
        {memberCount > 0 && (
          <div className="flex items-center gap-1 text-gray-400">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path d="M13 6a3 3 0 11-6 0 3 3 0 016 0zM18 8a2 2 0 11-4 0 2 2 0 014 0zM14 15a4 4 0 00-8 0v3h8v-3zM6 8a2 2 0 11-4 0 2 2 0 014 0zM16 18v-3a5.972 5.972 0 00-.75-2.906A3.005 3.005 0 0119 15v3h-3zM4.75 12.094A5.973 5.973 0 004 15v3H1v-3a3 3 0 013.75-2.906z" />
            </svg>
            <span className="text-sm">{memberCount}</span>
          </div>
        )}
        
        {/* Diğer işlem butonları */}
        <div className="flex items-center">
          {/* Arama butonu */}
          <button className="p-2 rounded-full text-gray-400 hover:text-white hover:bg-gray-700">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
            </svg>
          </button>
          
          {/* Bildirim ayarları */}
          <button className="p-2 rounded-full text-gray-400 hover:text-white hover:bg-gray-700">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path d="M10 2a6 6 0 00-6 6v3.586l-.707.707A1 1 0 004 14h12a1 1 0 00.707-1.707L16 11.586V8a6 6 0 00-6-6zM10 18a3 3 0 01-3-3h6a3 3 0 01-3 3z" />
            </svg>
          </button>
          
          {/* Kanal üyeleri */}
          <button className="p-2 rounded-full text-gray-400 hover:text-white hover:bg-gray-700">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path d="M9 6a3 3 0 11-6 0 3 3 0 016 0zM17 6a3 3 0 11-6 0 3 3 0 016 0zM12.93 17c.046-.327.07-.66.07-1a6.97 6.97 0 00-1.5-4.33A5 5 0 0119 16v1h-6.07zM6 11a5 5 0 015 5v1H1v-1a5 5 0 015-5z" />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
} 