"use client";

import React from "react";
import { useParams } from "next/navigation";
import MainLayout from "@/components/layout/MainLayout";
import ChatView from "@/components/chat/ChatView";

// Örnek kanal verileri
const dummyChannels = [
  { id: "1", name: "genel", description: "Genel sohbet kanalı", memberCount: 5 },
  { id: "2", name: "duyurular", description: "Önemli duyurular", memberCount: 3 },
  { id: "3", name: "proje-tartışma", description: "Proje ile ilgili tartışmalar", memberCount: 4 },
];

export default function ChannelPage() {
  const params = useParams();
  const channelId = params.channelId as string;
  
  // Aktif kanalı bul
  const activeChannel = dummyChannels.find((channel) => channel.id === channelId);
  
  return (
    <MainLayout>
      <ChatView 
        channelId={activeChannel?.id}
        channelName={activeChannel?.name || "bilinmeyen-kanal"}
        channelDescription={activeChannel?.description}
        memberCount={activeChannel?.memberCount}
      />
    </MainLayout>
  );
}