"use client";

import React from "react";

type User = {
  id: string;
  name: string;
  status: "online" | "idle" | "dnd" | "offline";
  avatar?: string;
};

// Örnek kullanıcı verisi
const dummyUsers: User[] = [
  { id: "1", name: "Ahmet Yılmaz", status: "online" },
  { id: "2", name: "Mehmet Koç", status: "idle" },
  { id: "3", name: "Ayşe Demir", status: "dnd" },
  { id: "4", name: "Fatma Öztürk", status: "offline" },
  { id: "5", name: "Mustafa Çelik", status: "online" },
];

export default function UserList() {
  // Kullanıcıları durumlarına göre gruplandır
  const onlineUsers = dummyUsers.filter((user) => user.status === "online");
  const idleUsers = dummyUsers.filter((user) => user.status === "idle");
  const dndUsers = dummyUsers.filter((user) => user.status === "dnd");
  const offlineUsers = dummyUsers.filter((user) => user.status === "offline");

  return (
    <div className="p-2 text-gray-300">
      <h3 className="px-2 uppercase text-xs font-semibold text-gray-400 tracking-wider mb-1">
        Çevrimiçi — {onlineUsers.length}
      </h3>
      <ul className="mb-4 space-y-[2px]">
        {onlineUsers.map((user) => (
          <UserItem key={user.id} user={user} />
        ))}
      </ul>

      {idleUsers.length > 0 && (
        <>
          <h3 className="px-2 uppercase text-xs font-semibold text-gray-400 tracking-wider mb-1">
            Boşta — {idleUsers.length}
          </h3>
          <ul className="mb-4 space-y-[2px]">
            {idleUsers.map((user) => (
              <UserItem key={user.id} user={user} />
            ))}
          </ul>
        </>
      )}

      {dndUsers.length > 0 && (
        <>
          <h3 className="px-2 uppercase text-xs font-semibold text-gray-400 tracking-wider mb-1">
            Rahatsız Etmeyin — {dndUsers.length}
          </h3>
          <ul className="mb-4 space-y-[2px]">
            {dndUsers.map((user) => (
              <UserItem key={user.id} user={user} />
            ))}
          </ul>
        </>
      )}

      {offlineUsers.length > 0 && (
        <>
          <h3 className="px-2 uppercase text-xs font-semibold text-gray-400 tracking-wider mb-1">
            Çevrimdışı — {offlineUsers.length}
          </h3>
          <ul className="mb-4 space-y-[2px]">
            {offlineUsers.map((user) => (
              <UserItem key={user.id} user={user} />
            ))}
          </ul>
        </>
      )}
    </div>
  );
}

function UserItem({ user }: { user: User }) {
  // Kullanıcı durumuna göre renk ve simge belirle
  const statusColors = {
    online: "bg-green-500",
    idle: "bg-yellow-500",
    dnd: "bg-red-500",
    offline: "bg-gray-500",
  };

  // Avatar adını ilk harflerinden oluştur
  const getInitials = (name: string) => {
    return name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase()
      .substring(0, 2);
  };

  return (
    <li className="flex items-center gap-2 rounded px-2 py-1 hover:bg-[#393c43] cursor-pointer">
      <div className="relative">
        <div className="w-8 h-8 rounded-full bg-gray-600 flex items-center justify-center text-white text-sm font-medium">
          {user.avatar || getInitials(user.name)}
        </div>
        <div
          className={`absolute -bottom-0.5 -right-0.5 w-3.5 h-3.5 rounded-full border-2 border-[#36393f] ${
            statusColors[user.status]
          }`}
        ></div>
      </div>
      <span className="text-sm">{user.name}</span>
    </li>
  );
} 