"use client";

import React from "react";

type Message = {
  id: string;
  content: string;
  timestamp: string;
  sender: {
    id: string;
    name: string;
    avatar?: string;
  };
};

// Örnek mesaj verisi
const dummyMessages: Message[] = [
  {
    id: "1",
    content: "Merhaba, bugün yeni proje üzerinde çalışmaya başlayacağız.",
    timestamp: "10:15",
    sender: {
      id: "1",
      name: "Ahmet Yılmaz",
    },
  },
  {
    id: "2",
    content: "Harika! Hangi görevleri tamamlamamız gerekiyor?",
    timestamp: "10:16",
    sender: {
      id: "2",
      name: "Mehmet Koç",
    },
  },
  {
    id: "3",
    content: "Şimdiki hedefimiz kullanıcı arayüzü tasarımını bitirmek olacak.",
    timestamp: "10:18",
    sender: {
      id: "1",
      name: "Ahmet Yılmaz",
    },
  },
  {
    id: "4",
    content: "Ben de backend API'lerine başlayacağım.",
    timestamp: "10:20",
    sender: {
      id: "3",
      name: "Ayşe Demir",
    },
  },
  {
    id: "5",
    content: "Bugün toplantımız saat kaçta?",
    timestamp: "10:25",
    sender: {
      id: "2",
      name: "Mehmet Koç",
    },
  },
];

export default function MessageList() {
  return (
    <div className="flex-1 overflow-y-auto px-4 py-6">
      <div className="space-y-4">
        {dummyMessages.map((message) => (
          <MessageItem key={message.id} message={message} />
        ))}
      </div>
    </div>
  );
}

function MessageItem({ message }: { message: Message }) {
  // Avatar adını ilk harflerinden oluştur
  const getInitials = (name: string) => {
    return name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase()
      .substring(0, 2);
  };

  // Kullanıcı ID'sinden rastgele renk oluştur
  const getUserColor = (id: string) => {
    const colors = [
      "bg-blue-500",
      "bg-green-500",
      "bg-purple-500",
      "bg-yellow-500",
      "bg-red-500",
      "bg-pink-500",
    ];
    const index = parseInt(id) % colors.length;
    return colors[index];
  };

  return (
    <div className="flex items-start group hover:bg-gray-800/20 -mx-2 p-2 rounded">
      <div className={`w-10 h-10 rounded-full ${getUserColor(message.sender.id)} flex-shrink-0 flex items-center justify-center text-white font-medium`}>
        {message.sender.avatar || getInitials(message.sender.name)}
      </div>
      <div className="ml-3 flex-1 min-w-0">
        <div className="flex items-baseline">
          <h4 className="font-medium text-white text-base">{message.sender.name}</h4>
          <span className="ml-2 text-xs text-gray-400">{message.timestamp}</span>
        </div>
        <p className="text-gray-300 mt-0.5 break-words">{message.content}</p>
      </div>
      <div className="flex-shrink-0 opacity-0 group-hover:opacity-100 transition-opacity">
        <button className="text-gray-400 hover:text-white p-1">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
            <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
          </svg>
        </button>
      </div>
    </div>
  );
} 